// ==============================
// Variables
// ==============================

// ========== Global ========== //
// Font and Line Height
$global-font-family: $font-mktg;
$global-font-size: $body-font-size;
$global-font-weight: $font-weight-normal;
$global-line-height: $body-line-height;

// Color of the background
$global-background-color: var(--color-bg-primary) !default;
$global-background-color-dark: var(--color-bg-primary) !default;

// Color of the text
$global-font-color: var(--color-text-primary) !default;
$global-font-color-dark: var(--color-text-primary) !default;

// Color of the secondary text
$global-font-secondary-color: var(--color-text-secondary) !default;
$global-font-secondary-color-dark: var(--color-text-secondary) !default;

// Color of the link
$global-link-color: var(--color-text-link) !default;
$global-link-color-dark: var(--color-text-link) !default;

// Color of the hover link
$global-link-hover-color: var(--color-text-link) !default;
$global-link-hover-color-dark: var(--color-text-link) !default;

// Color of the border
$global-border-color: var(--color-border-primary) !default;
$global-border-color-dark: var(--color-border-primary) !default;
// ========== Global ========== //

// Color of the header background
$header-background-color: var(--color-bg-primary) !default;
$header-background-color-dark: var(--color-bg-primary) !default;

// ========== Single Content ========== //
// Font size of the TOC
$toc-title-font-size: $h4-size !default;
$toc-content-font-size: $body-font-size !default;

// Color of the single link
$single-link-color: var(--color-text-link) !default;
$single-link-color-dark: var(--color-text-link) !default;

// Color of the hover single link
$single-link-hover-color: var(--color-text-danger) !default;
$single-link-hover-color-dark: var(--color-text-danger) !default;

// Color of the table background
$table-background-color: var(--color-bg-primary) !default;
$table-background-color-dark: var(--color-bg-primary) !default;

// Color of the table thead
$table-thead-color: var(--color-bg-tertiary) !default;
$table-thead-color-dark: var(--color-bg-tertiary) !default;

// Color of the blockquote
$blockquote-color: var(--color-bg-info) !default;
$blockquote-color-dark: var(--color-bg-info) !default;
// ========== Single Content ========== //

// ========== Pagination ========== //
// Color of the link in pagination
$pagination-link-color: var(--color-bg-info) !default;
$pagination-link-color-dark: var(--color-bg-info) !default;

// Color of the hover link in pagination
$pagination-link-hover-color: var(--color-bg-canvas-inverse) !default;
$pagination-link-hover-color-dark: var(--color-bg-canvas-inverse) !default;
// ========== Pagination ========== //

// ========== Code ========== //
// Color of the code
$code-color: var(--color-text-primary) !default;
$code-color-dark: var(--color-text-primary) !default;

// Color of the code background
$code-background-color: var(--color-bg-tertiary) !default;
$code-background-color-dark: var(--color-bg-tertiary) !default;

$code-info-color: var(--color-text-link) !default;
$code-info-color-dark: var(--color-text-link) !default;

// Font size of the code
$code-font-size: $body-font-size !default;

// Font family of the code
$code-font-family: Source Code Pro,
Menlo,
Consolas,
Monaco,
monospace,
$global-font-family !default;

// Code type map
$code-type-map: ( // Custom code type
  language-bash: 'Bash',
  language-c: 'C',
  language-cs: 'C#',
  language-cpp: 'C++',
  language-clojure: 'Clojure',
  language-coffeescript: 'CoffeeScript',
  language-css: 'CSS',
  language-dart: 'Dart',
  language-diff: 'Diff',
  language-erlang: 'Erlang',
  language-go: 'Go',
  language-go-html-template: 'Go HTML Template',
  language-groovy: 'Groovy',
  language-haskell: 'Haskell',
  language-html: 'HTML',
  language-http: 'HTTP',
  language-xml: 'XML',
  language-java: 'Java',
  language-js: 'JavaScript',
  language-javascript: 'JavaScript',
  language-json: 'JSON',
  language-kotlin: 'Kotlin',
  language-latex: 'LaTeX',
  language-less: 'Less',
  language-lisp: 'Lisp',
  language-lua: 'Lua',
  language-makefile: 'Makefile',
  language-markdown: 'Markdown',
  language-matlab: 'Matlab',
  language-objectivec: 'Objective-C',
  language-php: 'PHP',
  language-perl: 'Perl',
  language-python: 'Python',
  language-r: 'R',
  language-ruby: 'Ruby',
  language-rust: 'Rust',
  language-scala: 'Scala',
  language-scss: 'Scss',
  language-shell: 'Shell',
  language-sql: 'SQL',
  language-swift: 'Swift',
  language-tex: 'TeX',
  language-toml: 'TOML',
  language-ts: 'TypeScript',
  language-typescript: 'TypeScript',
  language-vue: 'Vue',
  language-yml: 'YAML',
  language-yaml: 'YAML',
) !default;

